"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import Link from "next/link"
import type { Assignment } from "@/lib/db"

export default function AssignmentsPage() {
  const [filter, setFilter] = useState("all")
  const [assignments, setAssignments] = useState<Assignment[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchAssignments = async () => {
      try {
        const response = await fetch('/api/assignments')
        if (!response.ok) {
          throw new Error('Failed to fetch assignments')
        }
        const data = await response.json()
        setAssignments(data)
      } catch (error) {
        console.error('Error fetching assignments:', error)
      } finally {
        setLoading(false)
      }
    }

    fetchAssignments()
  }, [])

  const filteredAssignments = filter === "all" 
    ? assignments
    : assignments.filter(assignment => assignment.status.toLowerCase() === filter)

  if (loading) {
    return (
      <div className="p-8">
        <div className="animate-pulse">
          <div className="h-8 bg-gray-200 rounded w-1/4 mb-4"></div>
          <div className="h-4 bg-gray-200 rounded w-1/3 mb-8"></div>
          <div className="space-y-6">
            {[1, 2, 3].map((i) => (
              <Card key={i} className="p-6">
                <div className="space-y-4">
                  <div className="h-6 bg-gray-200 rounded w-1/3"></div>
                  <div className="h-4 bg-gray-200 rounded w-1/4"></div>
                  <div className="h-4 bg-gray-200 rounded w-3/4"></div>
                  <div className="flex justify-between">
                    <div className="h-4 bg-gray-200 rounded w-1/5"></div>
                    <div className="h-4 bg-gray-200 rounded w-1/5"></div>
                  </div>
                </div>
              </Card>
            ))}
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="p-8 max-w-7xl mx-auto">
      <div className="flex justify-between items-center mb-8">
        <div>
          <h1 className="text-2xl font-bold mb-2">Assignments</h1>
          <p className="text-gray-600">Manage and track your course assignments</p>
        </div>
        <div className="flex items-center gap-4">
          <select
            className="px-4 py-2 border rounded-lg bg-white"
            value={filter}
            onChange={(e) => setFilter(e.target.value)}
          >
            <option value="all">All Assignments</option>
            <option value="pending">Pending</option>
            <option value="in progress">In Progress</option>
            <option value="completed">Completed</option>
          </select>
          <Link href="/assignments/new">
            <Button>
              New Assignment
            </Button>
          </Link>
        </div>
      </div>

      <div className="grid grid-cols-1 gap-6">
        {filteredAssignments.map((assignment) => (
          <Card key={assignment.id} className="p-6">
            <div className="flex items-start justify-between">
              <div className="space-y-1">
                <h3 className="text-lg font-semibold">{assignment.title}</h3>
                <p className="text-sm text-gray-500">{assignment.course_title}</p>
              </div>
              <span className={`px-3 py-1 rounded-full text-sm font-medium ${
                assignment.status === "completed"
                  ? "bg-green-100 text-green-800"
                  : assignment.status === "in progress"
                  ? "bg-blue-100 text-blue-800"
                  : "bg-yellow-100 text-yellow-800"
              }`}>
                {assignment.status}
              </span>
            </div>

            <p className="mt-4 text-gray-600">{assignment.description}</p>

            <div className="mt-6 flex items-center justify-between">
              <div className="flex items-center gap-6">
                <div>
                  <p className="text-sm text-gray-500">Due Date</p>
                  <p className="font-medium">
                    {new Date(assignment.due_date).toLocaleDateString()}
                  </p>
                </div>
                <div>
                  <p className="text-sm text-gray-500">Points</p>
                  <p className="font-medium">{assignment.points}</p>
                </div>
              </div>
            </div>

            <div className="mt-6 flex items-center justify-end gap-4">
              <Link href={`/assignments/${assignment.id}`}>
                <Button variant="outline">View Details</Button>
              </Link>
              {assignment.status !== "completed" && (
                <Button>Submit Assignment</Button>
              )}
            </div>
          </Card>
        ))}
      </div>

      {filteredAssignments.length === 0 && (
        <Card className="p-12 text-center">
          <p className="text-lg text-gray-500">No assignments found</p>
        </Card>
      )}
    </div>
  )
}
